import React, { useState, useEffect } from "react";
import { Helmet } from "react-helmet";
import { useHistory } from "react-router-dom";
import Navigation from "../components/navigation.jsx";
import Footer from "../components/footer.jsx";
import Notification from "../components/Notification.jsx";
import { authAPI } from "../services/api";
import { eventsService } from "../services/eventsService";
import * as Yup from "yup";
import "./event-management.css";

// Validation Schema vi Yup
const eventSchema = Yup.object().shape({
  title: Yup.string()
    .required("Tn s kin l bt buc")
    .min(10, "Tn s kin phi c t nht 10 k t")
    .max(200, "Tn s kin khng c qu 200 k t"),
  date: Yup.date()
    .required("Ngy t chc l bt buc")
    .min(new Date(), "Ngy t chc phi l ngy trong tng lai"),
  location: Yup.string()
    .required("a im l bt buc")
    .min(5, "a im phi c t nht 5 k t")
    .max(200, "a im khng c qu 200 k t"),
  description: Yup.string()
    .required("M t s kin l bt buc")
    .min(50, "M t phi c t nht 50 k t")
    .max(2000, "M t khng c qu 2000 k t"),
  maxParticipants: Yup.number()
    .required("S lng ngi tham gia l bt buc")
    .min(5, "S lng ti thiu l 5 ngi")
    .max(1000, "S lng ti a l 1000 ngi"),
  hours: Yup.number()
    .required("S gi tnh nguyn l bt buc")
    .min(1, "S gi ti thiu l 1 gi")
    .max(100, "S gi ti a l 100 gi"),
  category: Yup.string().required("Lnh vc l bt buc"),
});

const EventManagement = () => {
  const history = useHistory();
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const [showModal, setShowModal] = useState(false);
  const [editingEvent, setEditingEvent] = useState(null);
  const [formData, setFormData] = useState({
    title: "",
    date: "",
    location: "",
    description: "",
    maxParticipants: "",
    hours: "",
    category: "",
    image: "",
  });
  const [errors, setErrors] = useState({});
  const [events, setEvents] = useState([]);
  const [showRegistrationsModal, setShowRegistrationsModal] = useState(false);
  const [selectedEventForReview, setSelectedEventForReview] = useState(null);
  const [registrations, setRegistrations] = useState({
    pending: [],
    approved: [],
    rejected: [],
  });
  const [registrationsStats, setRegistrationsStats] = useState({
    total: 0,
    pending: 0,
    approved: 0,
    rejected: 0,
    checkedIn: 0,
    completed: 0,
  });
  const [notification, setNotification] = useState(null);
  const [confirmDialog, setConfirmDialog] = useState({
    show: false,
    message: "",
    onConfirm: null,
  });

  const showConfirm = (message, onConfirm) => {
    setConfirmDialog({ show: true, message, onConfirm });
  };

  const handleConfirm = () => {
    if (confirmDialog.onConfirm) {
      confirmDialog.onConfirm();
      // Don't close here, let the callback handle it
    }
  };

  const handleCancel = () => {
    setConfirmDialog({ show: false, message: "", onConfirm: null });
  };

  useEffect(() => {
    checkUserRole();
    loadEvents();
  }, []);

  const loadEvents = async () => {
    try {
      setLoading(true);
      const eventsData = await eventsService.getAllEvents();
      console.log("Loaded events:", eventsData);
      setEvents(eventsData);
    } catch (error) {
      console.error("Error loading events:", error);
    } finally {
      setLoading(false);
    }
  };

  const checkUserRole = async () => {
    try {
      const response = await authAPI.getMe();
      if (response.success) {
        const userData = response.data.user;
        setUser(userData);

        // Redirect nu khng phi event_manager hoc admin
        if (userData.role !== "event_manager" && userData.role !== "admin") {
          alert("Bn khng c quyn truy cp trang ny!");
          history.push("/");
        }
      }
    } catch (error) {
      console.error("Error fetching profile:", error);
      if (error.response?.status === 401) {
        history.push("/login");
      }
    } finally {
      setLoading(false);
    }
  };

  const getCategoryName = (category) => {
    const categories = {
      environment: " Mi trng",
      education: " Gio dc",
      youth: " Y t",
      elderly: " Ngi cao tui",
      disabled: " Ngi khuyt tt",
      healthcare: " Tr em",
    };
    return categories[category] || category;
  };

  const openCreateModal = () => {
    setEditingEvent(null);
    setFormData({
      title: "",
      date: "",
      location: "",
      description: "",
      maxParticipants: "",
      hours: "",
      category: "",
    });
    setErrors({});
    setShowModal(true);
  };

  const openEditModal = (event) => {
    setEditingEvent(event);
    setFormData({
      title: event.title,
      date: event.date,
      location: event.location,
      description: event.description,
      maxParticipants: event.maxParticipants,
      hours: event.hours,
      category: event.category,
    });
    setErrors({});
    setShowModal(true);
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
    // Clear error khi user bt u sa
    if (errors[name]) {
      setErrors((prev) => ({
        ...prev,
        [name]: "",
      }));
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setErrors({});

    try {
      // Validate vi Yup
      await eventSchema.validate(formData, { abortEarly: false });

      // S dng eventsService API
      if (editingEvent) {
        // Update event
        await eventsService.updateEvent(editingEvent._id, {
          ...formData,
          maxParticipants: parseInt(formData.maxParticipants),
          hours: parseInt(formData.hours),
        });
        setNotification({
          type: "success",
          title: "Thnh cng",
          message: "Cp nht s kin thnh cng!",
        });
      } else {
        // Create new event
        await eventsService.createEvent({
          ...formData,
          maxParticipants: parseInt(formData.maxParticipants),
          hours: parseInt(formData.hours),
          requirements: [
            " tui t 16 tr ln",
            "Sc khe tt",
            "Cam kt tham gia y ",
          ],
          benefits: [
            "Giy chng nhn tham gia",
            `${formData.hours} gi tnh nguyn`,
            "Kinh nghim thc t",
          ],
        });
        setNotification({
          type: "success",
          title: "Thnh cng",
          message: "To s kin mi thnh cng!",
        });
      }

      // Reload events sau khi to/sa
      await loadEvents();
      setShowModal(false);
    } catch (validationError) {
      if (validationError.inner) {
        // Yup validation errors
        const validationErrors = {};
        validationError.inner.forEach((error) => {
          validationErrors[error.path] = error.message;
        });
        setErrors(validationErrors);
      } else {
        // API errors
        const errorMessage =
          validationError.response?.data?.message ||
          "C li xy ra. Vui lng th li!";
        setErrors({ general: errorMessage });
      }
    }
  };

  const handleDelete = async (eventId) => {
    showConfirm(
      "Bn c chc chn mun xa s kin ny? Hnh ng ny khng th hon tc!",
      async () => {
        try {
          await eventsService.deleteEvent(eventId);
          await loadEvents();

          // Show notification IMMEDIATELY
          setNotification({
            type: "success",
            title: "Thnh cng",
            message: " xa s kin thnh cng!",
          });

          // Close confirm dialog after showing notification
          setConfirmDialog({ show: false, message: "", onConfirm: null });
        } catch (error) {
          const errorMessage =
            error.response?.data?.message || "C li khi xa s kin";

          // Show error notification IMMEDIATELY
          setNotification({
            type: "error",
            title: "Li",
            message: errorMessage,
          });

          // Close confirm dialog
          setConfirmDialog({ show: false, message: "", onConfirm: null });
        }
      }
    );
  };

  const handleViewRegistrations = async (event) => {
    try {
      setSelectedEventForReview(event);
      const response = await eventsService.getEventRegistrations(
        event._id || event.id
      );
      setRegistrations(response.data);
      setRegistrationsStats(response.statistics);
      setShowRegistrationsModal(true);
    } catch (error) {
      setNotification({
        type: "error",
        title: "Li",
        message:
          "C li khi ti danh sch ng k: " +
          (error.response?.data?.message || error.message),
      });
    }
  };

  const handleApproveRegistration = async (userId) => {
    try {
      await eventsService.approveRegistration(
        selectedEventForReview._id || selectedEventForReview.id,
        userId
      );

      // Reload registration data to update the modal
      const response = await eventsService.getEventRegistrations(
        selectedEventForReview._id || selectedEventForReview.id
      );
      setRegistrations(response.data);
      setRegistrationsStats(response.statistics);

      // Show notification immediately (modal stays open, no confirm dialog)
      setNotification({
        type: "success",
        title: "Thnh cng",
        message: " ph duyt ng k thnh cng!",
      });
    } catch (error) {
      setNotification({
        type: "error",
        title: "Li",
        message:
          "C li khi ph duyt: " +
          (error.response?.data?.message || error.message),
      });
    }
  };

  const handleRejectRegistration = async (userId) => {
    try {
      await eventsService.rejectRegistration(
        selectedEventForReview._id || selectedEventForReview.id,
        userId
      );

      // Reload registration data to update the modal
      const response = await eventsService.getEventRegistrations(
        selectedEventForReview._id || selectedEventForReview.id
      );
      setRegistrations(response.data);
      setRegistrationsStats(response.statistics);

      // Show notification
      setNotification({
        type: "success",
        title: "Thnh cng",
        message: " t chi ng k!",
      });
    } catch (error) {
      // Show error notification
      setNotification({
        type: "error",
        title: "Li",
        message:
          "C li khi t chi: " +
          (error.response?.data?.message || error.message),
      });
    }
  };

  const handleCheckIn = async (userId) => {
    try {
      await eventsService.checkInParticipant(
        selectedEventForReview._id || selectedEventForReview.id,
        userId
      );

      // Reload registration data to update the modal
      const response = await eventsService.getEventRegistrations(
        selectedEventForReview._id || selectedEventForReview.id
      );
      setRegistrations(response.data);
      setRegistrationsStats(response.statistics);

      // Show notification immediately (modal stays open)
      setNotification({
        type: "success",
        title: "Thnh cng",
        message: " check-in thnh cng!",
      });
    } catch (error) {
      setNotification({
        type: "error",
        title: "Li",
        message:
          "C li khi check-in: " +
          (error.response?.data?.message || error.message),
      });
    }
  };

  const handleUndoCheckIn = async (userId) => {
    try {
      await eventsService.undoCheckIn(
        selectedEventForReview._id || selectedEventForReview.id,
        userId
      );

      // Reload registration data to update the modal
      const response = await eventsService.getEventRegistrations(
        selectedEventForReview._id || selectedEventForReview.id
      );
      setRegistrations(response.data);
      setRegistrationsStats(response.statistics);

      // Show notification immediately
      setNotification({
        type: "success",
        title: "Thnh cng",
        message: " hy check-in!",
      });
    } catch (error) {
      // Show error notification immediately
      setNotification({
        type: "error",
        title: "Li",
        message:
          "C li khi hy check-in: " +
          (error.response?.data?.message || error.message),
      });
    }
  };

  const handleMarkCompleted = async (userId) => {
    try {
      await eventsService.markAsCompleted(
        selectedEventForReview._id || selectedEventForReview.id,
        userId
      );

      // Reload registration data to update the modal
      const response = await eventsService.getEventRegistrations(
        selectedEventForReview._id || selectedEventForReview.id
      );
      setRegistrations(response.data);
      setRegistrationsStats(response.statistics);

      // Show notification immediately (modal stays open)
      setNotification({
        type: "success",
        title: "Thnh cng",
        message: " nh du hon thnh!",
      });
    } catch (error) {
      setNotification({
        type: "error",
        title: "Li",
        message:
          "C li khi nh du hon thnh: " +
          (error.response?.data?.message || error.message),
      });
    }
  };

  const handleUndoCompleted = async (userId) => {
    try {
      await eventsService.undoCompleted(
        selectedEventForReview._id || selectedEventForReview.id,
        userId
      );

      // Reload registration data to update the modal
      const response = await eventsService.getEventRegistrations(
        selectedEventForReview._id || selectedEventForReview.id
      );
      setRegistrations(response.data);
      setRegistrationsStats(response.statistics);

      // Show notification immediately
      setNotification({
        type: "success",
        title: "Thnh cng",
        message: " hy hon thnh!",
      });
    } catch (error) {
      // Show error notification immediately
      setNotification({
        type: "error",
        title: "Li",
        message:
          "C li khi hy hon thnh: " +
          (error.response?.data?.message || error.message),
      });
    }
  };

  const handleCompleteEvent = async (eventId) => {
    showConfirm(
      "Bn c chc mun nh du hon thnh cho TT C ngi  check-in ca s kin ny?",
      async () => {
        try {
          const event = events.find(
            (e) => e._id === eventId || e.id === eventId
          );

          // Lc nhng ngi  check-in nhng cha completed
          const checkedInParticipants =
            event.participants?.filter((p) => p.checkedIn && !p.completed) ||
            [];

          if (checkedInParticipants.length === 0) {
            // Show notification IMMEDIATELY
            setNotification({
              type: "error",
              title: "Li",
              message:
                "Khng c ngi tham gia  check-in  nh du hon thnh!",
            });

            // Close confirm dialog after showing notification
            setConfirmDialog({ show: false, message: "", onConfirm: null });
            return;
          }

          // nh du hon thnh cho tng ngi
          for (const participant of checkedInParticipants) {
            await eventsService.markAsCompleted(
              eventId,
              participant.user?._id || participant.user
            );
          }

          await loadEvents();

          // Show notification IMMEDIATELY
          setNotification({
            type: "success",
            title: "Thnh cng",
            message: ` nh du hon thnh cho ${checkedInParticipants.length} ngi tham gia!`,
          });

          // Close confirm dialog after showing notification
          setConfirmDialog({ show: false, message: "", onConfirm: null });
        } catch (error) {
          // Show error notification IMMEDIATELY
          setNotification({
            type: "error",
            title: "Li",
            message:
              "C li khi hon thnh s kin: " +
              (error.response?.data?.message || error.message),
          });

          // Close confirm dialog
          setConfirmDialog({ show: false, message: "", onConfirm: null });
        }
      }
    );
  };

  const handlePrintParticipants = () => {
    window.print();
  };

  if (loading) {
    return (
      <div className="event-management-container">
        <Navigation />
        <div className="event-loading">
          <div className="spinner"></div>
          <p>ang ti...</p>
        </div>
        <Footer />
      </div>
    );
  }

  if (!user || (user.role !== "event_manager" && user.role !== "admin")) {
    return null;
  }

  return (
    <div className="event-management-container">
      <Helmet>
        <title>Qun L S Kin - VolunteerHub</title>
        <meta property="og:title" content="Qun L S Kin - VolunteerHub" />
      </Helmet>

      <Navigation />

      {confirmDialog.show && (
        <div className="modal-overlay" onClick={handleCancel}>
          <div
            className="modal-content confirm-dialog"
            onClick={(e) => e.stopPropagation()}
          >
            <h3 className="confirm-title">Xc nhn</h3>
            <p className="confirm-message">{confirmDialog.message}</p>
            <div className="confirm-actions">
              <button className="btn btn-secondary" onClick={handleCancel}>
                Hy
              </button>
              <button className="btn btn-primary" onClick={handleConfirm}>
                OK
              </button>
            </div>
          </div>
        </div>
      )}

      <div className="event-management-wrapper">
        {/* Header */}
        <div className="event-management-header">
          <div className="header-content">
            <h1 className="header-title">Qun l s kin</h1>
            <p className="header-description">
              To, chnh sa v qun l cc s kin tnh nguyn
            </p>
          </div>
          <button className="btn btn-primary" onClick={openCreateModal}>
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="20"
              height="20"
              viewBox="0 0 24 24"
              fill="none"
              stroke="currentColor"
              strokeWidth="2"
              strokeLinecap="round"
              strokeLinejoin="round"
            >
              <line x1="12" y1="5" x2="12" y2="19" />
              <line x1="5" y1="12" x2="19" y2="12" />
            </svg>
            To s kin mi
          </button>
        </div>

        {/* Events List */}
        <div className="events-grid">
          {console.log("Events array:", events, "Length:", events.length)}
          {events.length === 0 ? (
            <div className="empty-state">
              <div className="empty-icon"></div>
              <h3>Cha c s kin no</h3>
              <p>Bt u to s kin tnh nguyn u tin ca bn!</p>
              <button className="btn btn-primary" onClick={openCreateModal}>
                To s kin ngay
              </button>
            </div>
          ) : (
            events.map((event) => {
              console.log("Rendering event:", event.title);
              return (
                <div key={event._id || event.id} className="event-card">
                  <div className="event-image">
                    <img src={event.image} alt={event.title} />
                    <span className="event-category-badge">
                      {getCategoryName(event.category)}
                    </span>
                  </div>
                  <div className="event-content">
                    <h3 className="event-title">{event.title}</h3>

                    <div className="event-info-grid">
                      <div className="info-item">
                        <svg
                          xmlns="http://www.w3.org/2000/svg"
                          width="16"
                          height="16"
                          viewBox="0 0 24 24"
                          fill="none"
                          stroke="currentColor"
                          strokeWidth="2"
                          strokeLinecap="round"
                          strokeLinejoin="round"
                        >
                          <rect
                            x="3"
                            y="4"
                            width="18"
                            height="18"
                            rx="2"
                            ry="2"
                          />
                          <line x1="16" y1="2" x2="16" y2="6" />
                          <line x1="8" y1="2" x2="8" y2="6" />
                          <line x1="3" y1="10" x2="21" y2="10" />
                        </svg>
                        <span>
                          {new Date(event.date).toLocaleDateString("vi-VN")}
                        </span>
                      </div>
                      <div className="info-item">
                        <svg
                          xmlns="http://www.w3.org/2000/svg"
                          width="16"
                          height="16"
                          viewBox="0 0 24 24"
                          fill="none"
                          stroke="currentColor"
                          strokeWidth="2"
                          strokeLinecap="round"
                          strokeLinejoin="round"
                        >
                          <path d="M21 10c0 7-9 13-9 13s-9-6-9-13a9 9 0 0 1 18 0z" />
                          <circle cx="12" cy="10" r="3" />
                        </svg>
                        <span>{event.location}</span>
                      </div>
                      <div className="info-item">
                        <svg
                          xmlns="http://www.w3.org/2000/svg"
                          width="16"
                          height="16"
                          viewBox="0 0 24 24"
                          fill="none"
                          stroke="currentColor"
                          strokeWidth="2"
                          strokeLinecap="round"
                          strokeLinejoin="round"
                        >
                          <path d="M17 21v-2a4 4 0 0 0-4-4H5a4 4 0 0 0-4 4v2" />
                          <circle cx="9" cy="7" r="4" />
                          <path d="M23 21v-2a4 4 0 0 0-3-3.87" />
                          <path d="M16 3.13a4 4 0 0 1 0 7.75" />
                        </svg>
                        <span>
                          {event.registered}/{event.maxParticipants} ngi
                        </span>
                      </div>
                      <div className="info-item">
                        <svg
                          xmlns="http://www.w3.org/2000/svg"
                          width="16"
                          height="16"
                          viewBox="0 0 24 24"
                          fill="none"
                          stroke="currentColor"
                          strokeWidth="2"
                          strokeLinecap="round"
                          strokeLinejoin="round"
                        >
                          <circle cx="12" cy="12" r="10" />
                          <polyline points="12 6 12 12 16 14" />
                        </svg>
                        <span>{event.hours} gi</span>
                      </div>
                    </div>

                    <p className="event-description">{event.description}</p>

                    <div className="event-actions">
                      <button
                        className="btn btn-info"
                        onClick={() => handleViewRegistrations(event)}
                        title="Xem danh sch ng k"
                      >
                        <svg
                          xmlns="http://www.w3.org/2000/svg"
                          width="18"
                          height="18"
                          viewBox="0 0 24 24"
                          fill="none"
                          stroke="currentColor"
                          strokeWidth="2"
                          strokeLinecap="round"
                          strokeLinejoin="round"
                        >
                          <path d="M17 21v-2a4 4 0 0 0-4-4H5a4 4 0 0 0-4 4v2" />
                          <circle cx="9" cy="7" r="4" />
                          <path d="M23 21v-2a4 4 0 0 0-3-3.87" />
                          <path d="M16 3.13a4 4 0 0 1 0 7.75" />
                        </svg>
                        Qun l ng k
                        {event.participants?.filter(
                          (p) => p.status === "pending"
                        ).length > 0 && (
                          <span className="badge-notification">
                            {
                              event.participants.filter(
                                (p) => p.status === "pending"
                              ).length
                            }
                          </span>
                        )}
                      </button>
                      <button
                        className="btn btn-outline"
                        onClick={() => openEditModal(event)}
                      >
                        <svg
                          xmlns="http://www.w3.org/2000/svg"
                          width="18"
                          height="18"
                          viewBox="0 0 24 24"
                          fill="none"
                          stroke="currentColor"
                          strokeWidth="2"
                          strokeLinecap="round"
                          strokeLinejoin="round"
                        >
                          <path d="M11 4H4a2 2 0 0 0-2 2v14a2 2 0 0 0 2 2h14a2 2 0 0 0 2-2v-7" />
                          <path d="M18.5 2.5a2.121 2.121 0 0 1 3 3L12 15l-4 1 1-4 9.5-9.5z" />
                        </svg>
                        Sa
                      </button>
                      {new Date(event.date) < new Date() && (
                        <button
                          className="btn btn-success"
                          onClick={() =>
                            handleCompleteEvent(event._id || event.id)
                          }
                          title="nh du hon thnh cho tt c ngi tham gia"
                        >
                          <svg
                            xmlns="http://www.w3.org/2000/svg"
                            width="18"
                            height="18"
                            viewBox="0 0 24 24"
                            fill="none"
                            stroke="currentColor"
                            strokeWidth="2"
                            strokeLinecap="round"
                            strokeLinejoin="round"
                          >
                            <polyline points="20 6 9 17 4 12" />
                          </svg>
                          Hon thnh
                        </button>
                      )}
                      <button
                        className="btn btn-danger"
                        onClick={() => handleDelete(event._id || event.id)}
                      >
                        <svg
                          xmlns="http://www.w3.org/2000/svg"
                          width="18"
                          height="18"
                          viewBox="0 0 24 24"
                          fill="none"
                          stroke="currentColor"
                          strokeWidth="2"
                          strokeLinecap="round"
                          strokeLinejoin="round"
                        >
                          <polyline points="3 6 5 6 21 6" />
                          <path d="M19 6v14a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2V6m3 0V4a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v2" />
                        </svg>
                        Xa
                      </button>
                    </div>
                  </div>
                </div>
              );
            })
          )}
        </div>
      </div>

      {/* Modal Create/Edit Event */}
      {showModal && (
        <div className="modal-overlay" onClick={() => setShowModal(false)}>
          <div className="modal-content" onClick={(e) => e.stopPropagation()}>
            <div className="modal-header">
              <h2>{editingEvent ? "Chnh sa s kin" : "To s kin mi"}</h2>
            </div>

            <form onSubmit={handleSubmit} className="modal-form">
              <div className="form-group">
                <label htmlFor="title" className="form-label">
                  Tn s kin <span className="required">*</span>
                </label>
                <input
                  type="text"
                  id="title"
                  name="title"
                  className={`form-input ${errors.title ? "error" : ""}`}
                  value={formData.title}
                  onChange={handleInputChange}
                  placeholder="VD: Chin dch trng cy xanh ti H Ni"
                />
                {errors.title && (
                  <span className="error-text">{errors.title}</span>
                )}
              </div>

              <div className="form-row">
                <div className="form-group">
                  <label htmlFor="date" className="form-label">
                    Ngy t chc <span className="required">*</span>
                  </label>
                  <input
                    type="date"
                    id="date"
                    name="date"
                    className={`form-input ${errors.date ? "error" : ""}`}
                    value={formData.date}
                    onChange={handleInputChange}
                  />
                  {errors.date && (
                    <span className="error-text">{errors.date}</span>
                  )}
                </div>

                <div className="form-group">
                  <label htmlFor="category" className="form-label">
                    Lnh vc <span className="required">*</span>
                  </label>
                  <select
                    id="category"
                    name="category"
                    className={`form-input ${errors.category ? "error" : ""}`}
                    value={formData.category}
                    onChange={handleInputChange}
                  >
                    <option value="">-- Chn lnh vc --</option>
                    <option value="environment"> Mi trng</option>
                    <option value="education"> Gio dc</option>
                    <option value="youth"> Y t</option>
                    <option value="elderly"> Ngi cao tui</option>
                    <option value="disabled"> Ngi khuyt tt</option>
                    <option value="healthcare"> Tr em</option>
                  </select>
                  {errors.category && (
                    <span className="error-text">{errors.category}</span>
                  )}
                </div>
              </div>

              <div className="form-group">
                <label htmlFor="location" className="form-label">
                  a im <span className="required">*</span>
                </label>
                <input
                  type="text"
                  id="location"
                  name="location"
                  className={`form-input ${errors.location ? "error" : ""}`}
                  value={formData.location}
                  onChange={handleInputChange}
                  placeholder="VD: Cng vin Thng Nht, H Ni"
                />
                {errors.location && (
                  <span className="error-text">{errors.location}</span>
                )}
              </div>

              <div className="form-row">
                <div className="form-group">
                  <label htmlFor="maxParticipants" className="form-label">
                    S lng ngi tham gia <span className="required">*</span>
                  </label>
                  <input
                    type="number"
                    id="maxParticipants"
                    name="maxParticipants"
                    className={`form-input ${
                      errors.maxParticipants ? "error" : ""
                    }`}
                    value={formData.maxParticipants}
                    onChange={handleInputChange}
                    placeholder="VD: 100"
                    min="5"
                    max="1000"
                  />
                  {errors.maxParticipants && (
                    <span className="error-text">{errors.maxParticipants}</span>
                  )}
                </div>

                <div className="form-group">
                  <label htmlFor="hours" className="form-label">
                    S gi tnh nguyn <span className="required">*</span>
                  </label>
                  <input
                    type="number"
                    id="hours"
                    name="hours"
                    className={`form-input ${errors.hours ? "error" : ""}`}
                    value={formData.hours}
                    onChange={handleInputChange}
                    placeholder="VD: 4"
                    min="1"
                    max="100"
                  />
                  {errors.hours && (
                    <span className="error-text">{errors.hours}</span>
                  )}
                </div>
              </div>

              <div className="form-group">
                <label htmlFor="description" className="form-label">
                  M t s kin <span className="required">*</span>
                </label>
                <textarea
                  id="description"
                  name="description"
                  className={`form-textarea ${
                    errors.description ? "error" : ""
                  }`}
                  value={formData.description}
                  onChange={handleInputChange}
                  placeholder="M t chi tit v s kin, mc ch, yu cu i vi tnh nguyn vin..."
                  rows="5"
                />
                {errors.description && (
                  <span className="error-text">{errors.description}</span>
                )}
                <small className="form-hint">
                  {formData.description.length}/2000 k t (ti thiu 50 k t)
                </small>
              </div>

              {errors.general && (
                <div className="alert alert-error">{errors.general}</div>
              )}

              <div className="modal-actions">
                <button
                  type="button"
                  className="btn btn-outline"
                  onClick={() => setShowModal(false)}
                >
                  Hy
                </button>
                <button type="submit" className="btn btn-primary">
                  {editingEvent ? "Cp nht" : "To s kin"}
                </button>
              </div>
            </form>
          </div>
        </div>
      )}

      {/* Modal Qun l ng k */}
      {showRegistrationsModal && selectedEventForReview && (
        <div
          className="modal-overlay"
          onClick={() => setShowRegistrationsModal(false)}
        >
          <div
            className="modal-content modal-large"
            onClick={(e) => e.stopPropagation()}
          >
            <div className="modal-header">
              <h2>Qun l ng k - {selectedEventForReview.title}</h2>
            </div>

            <div className="registrations-stats">
              <div className="stat-card">
                <div className="stat-number">{registrationsStats.total}</div>
                <div className="stat-label">Tng ng k</div>
              </div>
              <div className="stat-card stat-pending">
                <div className="stat-number">{registrationsStats.pending}</div>
                <div className="stat-label">Ch duyt</div>
              </div>
              <div className="stat-card stat-approved">
                <div className="stat-number">{registrationsStats.approved}</div>
                <div className="stat-label"> duyt</div>
              </div>
              <div className="stat-card stat-rejected">
                <div className="stat-number">{registrationsStats.rejected}</div>
                <div className="stat-label"> t chi</div>
              </div>
              <div className="stat-card stat-checkedin">
                <div className="stat-number">
                  {registrationsStats.checkedIn}
                </div>
                <div className="stat-label"> check-in</div>
              </div>
              <div className="stat-card stat-completed">
                <div className="stat-number">
                  {registrationsStats.completed}
                </div>
                <div className="stat-label"> hon thnh</div>
              </div>
            </div>

            <div className="registration-actions-header">
              <button
                className="btn btn-primary"
                onClick={handlePrintParticipants}
              >
                 In danh sch
              </button>
            </div>

            <div className="registrations-tabs">
              <div className="tab-content">
                {/* Danh sch ch duyt */}
                {registrations.pending.length > 0 && (
                  <div className="registration-section">
                    <h3 className="section-title">
                       Ch ph duyt ({registrations.pending.length})
                    </h3>
                    <div className="registrations-list">
                      {registrations.pending.map((reg) => (
                        <div
                          key={reg._id}
                          className="registration-item pending"
                        >
                          <div className="registration-info">
                            <div className="user-avatar">
                              {reg.user?.username?.charAt(0).toUpperCase() ||
                                "U"}
                            </div>
                            <div className="user-details">
                              <div className="user-name">
                                {reg.user?.username || "N/A"}
                              </div>
                              <div className="user-email">
                                {reg.user?.email || "N/A"}
                              </div>
                              <div className="registration-date">
                                ng k:{" "}
                                {new Date(reg.registeredAt).toLocaleString(
                                  "vi-VN"
                                )}
                              </div>
                            </div>
                          </div>
                          <div className="registration-actions">
                            <button
                              className="btn btn-success btn-sm"
                              onClick={() =>
                                handleApproveRegistration(reg.user._id)
                              }
                            >
                               Ph duyt
                            </button>
                            <button
                              className="btn btn-danger btn-sm"
                              onClick={() =>
                                handleRejectRegistration(reg.user._id)
                              }
                            >
                               T chi
                            </button>
                          </div>
                        </div>
                      ))}
                    </div>
                  </div>
                )}

                {/* Danh sch  duyt */}
                {registrations.approved.length > 0 && (
                  <div className="registration-section">
                    <h3 className="section-title">
                        ph duyt ({registrations.approved.length})
                    </h3>
                    <div className="registrations-list">
                      {registrations.approved.map((reg) => (
                        <div
                          key={reg._id}
                          className="registration-item approved"
                        >
                          <div className="registration-info">
                            <div className="user-avatar">
                              {reg.user?.username?.charAt(0).toUpperCase() ||
                                "U"}
                            </div>
                            <div className="user-details">
                              <div className="user-name">
                                {reg.user?.username || "N/A"}
                              </div>
                              <div className="user-email">
                                {reg.user?.email || "N/A"}
                              </div>
                              <div className="registration-date">
                                Ph duyt:{" "}
                                {reg.reviewedAt
                                  ? new Date(reg.reviewedAt).toLocaleString(
                                      "vi-VN"
                                    )
                                  : "N/A"}
                              </div>
                              {reg.checkedIn && (
                                <div className="registration-date">
                                   Check-in:{" "}
                                  {reg.checkInTime
                                    ? new Date(reg.checkInTime).toLocaleString(
                                        "vi-VN"
                                      )
                                    : "N/A"}
                                </div>
                              )}
                              {reg.completed && (
                                <div className="registration-date">
                                   Hon thnh:{" "}
                                  {reg.completedAt
                                    ? new Date(reg.completedAt).toLocaleString(
                                        "vi-VN"
                                      )
                                    : "N/A"}
                                </div>
                              )}
                            </div>
                          </div>
                          <div className="registration-actions">
                            {!reg.checkedIn && (
                              <button
                                className="btn btn-info btn-sm"
                                onClick={() => handleCheckIn(reg.user._id)}
                              >
                                 Check-in
                              </button>
                            )}
                            {reg.checkedIn && !reg.completed && (
                              <>
                                <button
                                  className="btn btn-warning btn-sm"
                                  onClick={() =>
                                    handleUndoCheckIn(reg.user._id)
                                  }
                                >
                                   Hy check-in
                                </button>
                                <button
                                  className="btn btn-success btn-sm"
                                  onClick={() =>
                                    handleMarkCompleted(reg.user._id)
                                  }
                                >
                                   Hon thnh
                                </button>
                              </>
                            )}
                            {reg.completed && (
                              <button
                                className="btn btn-secondary btn-sm"
                                onClick={() =>
                                  handleUndoCompleted(reg.user._id)
                                }
                              >
                                 Hy hon thnh
                              </button>
                            )}
                          </div>
                        </div>
                      ))}
                    </div>
                  </div>
                )}

                {/* Danh sch  t chi */}
                {registrations.rejected.length > 0 && (
                  <div className="registration-section">
                    <h3 className="section-title">
                        t chi ({registrations.rejected.length})
                    </h3>
                    <div className="registrations-list">
                      {registrations.rejected.map((reg) => (
                        <div
                          key={reg._id}
                          className="registration-item rejected"
                        >
                          <div className="registration-info">
                            <div className="user-avatar">
                              {reg.user?.username?.charAt(0).toUpperCase() ||
                                "U"}
                            </div>
                            <div className="user-details">
                              <div className="user-name">
                                {reg.user?.username || "N/A"}
                              </div>
                              <div className="user-email">
                                {reg.user?.email || "N/A"}
                              </div>
                              <div className="registration-date">
                                T chi:{" "}
                                {reg.reviewedAt
                                  ? new Date(reg.reviewedAt).toLocaleString(
                                      "vi-VN"
                                    )
                                  : "N/A"}
                              </div>
                            </div>
                          </div>
                          <div className="status-badge status-rejected">
                             t chi
                          </div>
                        </div>
                      ))}
                    </div>
                  </div>
                )}

                {registrations.pending.length === 0 &&
                  registrations.approved.length === 0 &&
                  registrations.rejected.length === 0 && (
                    <div className="empty-state">
                      <p>Cha c ng k no cho s kin ny</p>
                    </div>
                  )}
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Notification - Render cui cng  hin th trn cng */}
      {notification && (
        <Notification
          type={notification.type}
          title={notification.title}
          message={notification.message}
          onClose={() => setNotification(null)}
        />
      )}

      <Footer />
    </div>
  );
};

export default EventManagement;
