import React, { useState, useEffect } from "react";
import { Helmet } from "react-helmet";
import { useHistory } from "react-router-dom";
import Navigation from "../components/navigation";
import Footer from "../components/footer";
import Notification from "../components/Notification";
import { adminService } from "../services/adminService";
import { eventsService } from "../services/eventsService";
import { authAPI } from "../services/api";
import "./admin-dashboard.css";

const AdminDashboard = () => {
  const history = useHistory();
  const [loading, setLoading] = useState(true);
  const [activeTab, setActiveTab] = useState("overview"); // overview, users, events
  const [users, setUsers] = useState([]);
  const [events, setEvents] = useState([]);
  const [stats, setStats] = useState({});
  const [searchTerm, setSearchTerm] = useState("");
  const [selectedUser, setSelectedUser] = useState(null);
  const [showUserModal, setShowUserModal] = useState(false);
  const [notification, setNotification] = useState(null);
  const [confirmDialog, setConfirmDialog] = useState({
    show: false,
    message: "",
    onConfirm: null,
  });

  const showConfirm = (message, onConfirm) => {
    setConfirmDialog({ show: true, message, onConfirm });
  };

  const handleConfirm = () => {
    if (confirmDialog.onConfirm) {
      confirmDialog.onConfirm();
    }
    setConfirmDialog({ show: false, message: "", onConfirm: null });
  };

  const handleCancel = () => {
    setConfirmDialog({ show: false, message: "", onConfirm: null });
  };

  useEffect(() => {
    checkAdminAccess();
    fetchData();
  }, []);

  const checkAdminAccess = () => {
    const user = authAPI.getUserData();
    if (user?.role !== "admin") {
      setNotification({
        type: "error",
        title: "Li!",
        message: "Bn khng c quyn truy cp trang ny!",
      });
      setTimeout(() => history.push("/"), 2000);
    }
  };

  const fetchData = async () => {
    try {
      setLoading(true);

      // Fetch statistics
      const statsData = await adminService.getUserStats();
      setStats(statsData.data);

      // Fetch users
      const usersData = await adminService.getAllUsers();
      setUsers(usersData.data);

      // Fetch events
      const eventsData = await eventsService.getAllEvents();
      setEvents(eventsData.data || eventsData);
    } catch (error) {
      console.error("Error fetching data:", error);
      if (error.response?.status === 401 || error.response?.status === 403) {
        setNotification({
          type: "error",
          title: "Li!",
          message: "Phin ng nhp ht hn hoc khng c quyn truy cp!",
        });
        setTimeout(() => history.push("/login"), 2000);
      }
    } finally {
      setLoading(false);
    }
  };

  const handleLockUser = async (userId) => {
    showConfirm("Bn c chc mun kha ti khon ny?", async () => {
      try {
        await adminService.lockUser(userId);

        // Cp nht state users thay v reload - s dng isActive
        setUsers((prevUsers) =>
          prevUsers.map((user) =>
            user._id === userId ? { ...user, isActive: false } : user
          )
        );

        setNotification({
          type: "success",
          title: "Thnh cng!",
          message: " kha ti khon thnh cng!",
        });
      } catch (error) {
        setNotification({
          type: "error",
          title: "Li!",
          message: error.response?.data?.message || "Khng th kha ti khon",
        });
      }
    });
  };

  const handleUnlockUser = async (userId) => {
    showConfirm("Bn c chc mun m kha ti khon ny?", async () => {
      try {
        await adminService.unlockUser(userId);

        // Cp nht state users thay v reload - s dng isActive
        setUsers((prevUsers) =>
          prevUsers.map((user) =>
            user._id === userId ? { ...user, isActive: true } : user
          )
        );

        setNotification({
          type: "success",
          title: "Thnh cng!",
          message: " m kha ti khon thnh cng!",
        });
      } catch (error) {
        setNotification({
          type: "error",
          title: "Li!",
          message:
            error.response?.data?.message || "Khng th m kha ti khon",
        });
      }
    });
  };

  const handleDeleteUser = async (userId) => {
    showConfirm(
      "Bn c chc mun XA ti khon ny? Hnh ng ny khng th hon tc!",
      async () => {
        try {
          await adminService.deleteUser(userId);

          // Xa user khi state thay v reload
          setUsers((prevUsers) =>
            prevUsers.filter((user) => user._id !== userId)
          );

          // Cp nht stats
          setStats((prevStats) => ({
            ...prevStats,
            totalUsers: (prevStats.totalUsers || 0) - 1,
          }));

          setNotification({
            type: "success",
            title: "Thnh cng!",
            message: " xa ti khon thnh cng!",
          });
        } catch (error) {
          setNotification({
            type: "error",
            title: "Li!",
            message: error.response?.data?.message || "Khng th xa ti khon",
          });
        }
      }
    );
  };

  const handleUpdateRole = async (userId, newRole) => {
    try {
      await adminService.updateUserRole(userId, newRole);

      // Cp nht role trong state thay v reload
      setUsers((prevUsers) =>
        prevUsers.map((user) =>
          user._id === userId ? { ...user, role: newRole } : user
        )
      );

      setNotification({
        type: "success",
        title: "Thnh cng!",
        message: " cp nht vai tr thnh cng!",
      });
      setShowUserModal(false);
    } catch (error) {
      setNotification({
        type: "error",
        title: "Li!",
        message: error.response?.data?.message || "Khng th cp nht vai tr",
      });
    }
  };

  const handleExportUsers = async (format) => {
    try {
      const result = await adminService.exportUsers(format);
      if (format === "csv") {
        setNotification({
          type: "success",
          title: "Thnh cng!",
          message: " ti xung file CSV!",
        });
      } else if (format === "json") {
        // Download JSON file
        const blob = new Blob([JSON.stringify(result, null, 2)], {
          type: "application/json",
        });
        const url = window.URL.createObjectURL(blob);
        const link = document.createElement("a");
        link.href = url;
        link.download = `users_${Date.now()}.json`;
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        window.URL.revokeObjectURL(url);
        setNotification({
          type: "success",
          title: "Thnh cng!",
          message: " ti xung file JSON!",
        });
      }
    } catch (error) {
      setNotification({
        type: "error",
        title: "Li!",
        message: "Khng th xut d liu: " + error.message,
      });
    }
  };

  const handleExportEvents = async (format) => {
    try {
      const result = await adminService.exportEvents(format);
      if (format === "csv") {
        setNotification({
          type: "success",
          title: "Thnh cng!",
          message: " ti xung file CSV!",
        });
      } else if (format === "json") {
        // Download JSON file
        const blob = new Blob([JSON.stringify(result, null, 2)], {
          type: "application/json",
        });
        const url = window.URL.createObjectURL(blob);
        const link = document.createElement("a");
        link.href = url;
        link.download = `events_${Date.now()}.json`;
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        window.URL.revokeObjectURL(url);
        setNotification({
          type: "success",
          title: "Thnh cng!",
          message: " ti xung file JSON!",
        });
      }
    } catch (error) {
      setNotification({
        type: "error",
        title: "Li!",
        message: "Khng th xut d liu: " + error.message,
      });
    }
  };

  const handleDeleteEvent = async (eventId) => {
    showConfirm("Bn c chc mun xa s kin ny?", async () => {
      try {
        await eventsService.deleteEvent(eventId);

        // Xa event khi state thay v reload
        setEvents((prevEvents) =>
          prevEvents.filter(
            (event) => event._id !== eventId && event.id !== eventId
          )
        );

        setNotification({
          type: "success",
          title: "Thnh cng!",
          message: " xa s kin thnh cng!",
        });
      } catch (error) {
        setNotification({
          type: "error",
          title: "Li!",
          message: error.response?.data?.message || "Khng th xa s kin",
        });
      }
    });
  };

  const filteredUsers = users.filter(
    (user) =>
      user.username?.toLowerCase().includes(searchTerm.toLowerCase()) ||
      user.fullName?.toLowerCase().includes(searchTerm.toLowerCase()) ||
      user.email?.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const filteredEvents = events.filter(
    (event) =>
      event.title?.toLowerCase().includes(searchTerm.toLowerCase()) ||
      event.location?.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const getRoleBadgeClass = (role) => {
    const roleMap = {
      admin: "role-admin",
      event_manager: "role-manager",
      volunteer: "role-volunteer",
    };
    return roleMap[role] || "role-volunteer";
  };

  const getRoleName = (role) => {
    const roleNames = {
      admin: "Qun tr vin",
      event_manager: "Qun l s kin",
      volunteer: "Tnh nguyn vin",
    };
    return roleNames[role] || role;
  };

  if (loading) {
    return (
      <div className="admin-dashboard-container">
        <Navigation />
        <div className="admin-loading">
          <div className="spinner"></div>
          <p>ang ti d liu...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="admin-dashboard-container">
      <Helmet>
        <title>Admin Dashboard - VolunteerHub</title>
      </Helmet>

      <Navigation />

      {/* Confirm Dialog */}
      {confirmDialog.show && (
        <div className="modal-overlay" onClick={handleCancel}>
          <div
            className="modal-content confirm-dialog"
            onClick={(e) => e.stopPropagation()}
          >
            <h3 className="confirm-title">Xc nhn</h3>
            <p className="confirm-message">{confirmDialog.message}</p>
            <div className="confirm-actions">
              <button className="btn btn-secondary" onClick={handleCancel}>
                Hy
              </button>
              <button className="btn btn-primary" onClick={handleConfirm}>
                OK
              </button>
            </div>
          </div>
        </div>
      )}

      <div className="admin-dashboard-wrapper">
        {/* Header */}
        <div className="admin-header">
          <div className="admin-header-content">
            <h1> Bng iu Khin Admin</h1>
            <p>Qun l ngi dng, s kin v h thng</p>
          </div>
        </div>

        {/* Tabs */}
        <div className="admin-tabs">
          <button
            className={`admin-tab ${activeTab === "overview" ? "active" : ""}`}
            onClick={() => setActiveTab("overview")}
          >
             Tng quan
          </button>
          <button
            className={`admin-tab ${activeTab === "users" ? "active" : ""}`}
            onClick={() => setActiveTab("users")}
          >
             Ngi dng ({users.length})
          </button>
          <button
            className={`admin-tab ${activeTab === "events" ? "active" : ""}`}
            onClick={() => setActiveTab("events")}
          >
             S kin ({events.length})
          </button>
        </div>

        {/* Content */}
        <div className="admin-content">
          {/* OVERVIEW TAB */}
          {activeTab === "overview" && (
            <div className="overview-section">
              <div className="stats-grid">
                <div className="stat-card">
                  <div className="stat-icon"></div>
                  <div className="stat-info">
                    <h3>{stats.totalUsers || 0}</h3>
                    <p>Tng ngi dng</p>
                  </div>
                </div>
                <div className="stat-card">
                  <div className="stat-icon"></div>
                  <div className="stat-info">
                    <h3>{stats.activeUsers || 0}</h3>
                    <p>Ti khon hot ng</p>
                  </div>
                </div>
                <div className="stat-card">
                  <div className="stat-icon"></div>
                  <div className="stat-info">
                    <h3>{stats.lockedUsers || 0}</h3>
                    <p>Ti khon b kha</p>
                  </div>
                </div>
                <div className="stat-card">
                  <div className="stat-icon"></div>
                  <div className="stat-info">
                    <h3>{events.length}</h3>
                    <p>Tng s kin</p>
                  </div>
                </div>
                <div className="stat-card">
                  <div className="stat-icon"></div>
                  <div className="stat-info">
                    <h3>{stats.newUsers || 0}</h3>
                    <p>Ngi dng mi (30 ngy)</p>
                  </div>
                </div>
                <div className="stat-card">
                  <div className="stat-icon"></div>
                  <div className="stat-info">
                    <h3>{stats.usersByRole?.event_manager || 0}</h3>
                    <p>Qun l s kin</p>
                  </div>
                </div>
              </div>

              <div className="export-section">
                <h2> Xut D Liu</h2>
                <div className="export-buttons">
                  <button
                    className="btn btn-export"
                    onClick={() => handleExportUsers("csv")}
                  >
                     Xut Ngi Dng (CSV)
                  </button>
                  <button
                    className="btn btn-export"
                    onClick={() => handleExportEvents("csv")}
                  >
                     Xut S Kin (CSV)
                  </button>
                  <button
                    className="btn btn-export"
                    onClick={() => handleExportUsers("json")}
                  >
                     Xut Ngi Dng (JSON)
                  </button>
                  <button
                    className="btn btn-export"
                    onClick={() => handleExportEvents("json")}
                  >
                     Xut S Kin (JSON)
                  </button>
                </div>
              </div>
            </div>
          )}

          {/* USERS TAB */}
          {activeTab === "users" && (
            <div className="users-section">
              <div className="section-header">
                <input
                  type="text"
                  className="search-input"
                  placeholder=" Tm kim ngi dng..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                />
                <button
                  className="btn btn-export-sm"
                  onClick={() => handleExportUsers("csv")}
                >
                   Xut CSV
                </button>
              </div>

              <div className="users-table-wrapper">
                <table className="admin-table">
                  <thead>
                    <tr>
                      <th>Tn ng nhp</th>
                      <th>H tn</th>
                      <th>Email</th>
                      <th>Vai tr</th>
                      <th>Trng thi</th>
                      <th>S kin to</th>
                      <th>S kin tham gia</th>
                      <th>Thao tc</th>
                    </tr>
                  </thead>
                  <tbody>
                    {filteredUsers.map((user) => (
                      <tr key={user._id}>
                        <td>{user.username}</td>
                        <td>{user.fullName}</td>
                        <td>{user.email}</td>
                        <td>
                          <span
                            className={`role-badge ${getRoleBadgeClass(
                              user.role
                            )}`}
                          >
                            {getRoleName(user.role)}
                          </span>
                        </td>
                        <td>
                          <span
                            className={`status-badge ${
                              user.isActive ? "active" : "locked"
                            }`}
                          >
                            {user.isActive ? "Hot ng" : "B kha"}
                          </span>
                        </td>
                        <td>{user.stats?.eventsCreated || 0}</td>
                        <td>{user.stats?.eventsJoined || 0}</td>
                        <td>
                          <div className="action-buttons">
                            <button
                              className="btn-action btn-edit"
                              onClick={() => {
                                setSelectedUser(user);
                                setShowUserModal(true);
                              }}
                              title="Chnh sa"
                            >
                              
                            </button>
                            {user.isActive ? (
                              <button
                                className="btn-action btn-lock"
                                onClick={() => handleLockUser(user._id)}
                                title="Kha"
                              >
                                
                              </button>
                            ) : (
                              <button
                                className="btn-action btn-unlock"
                                onClick={() => handleUnlockUser(user._id)}
                                title="M kha"
                              >
                                
                              </button>
                            )}
                            {user.role !== "admin" && (
                              <button
                                className="btn-action btn-delete"
                                onClick={() => handleDeleteUser(user._id)}
                                title="Xa"
                              >
                                
                              </button>
                            )}
                          </div>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </div>
          )}

          {/* EVENTS TAB */}
          {activeTab === "events" && (
            <div className="events-section">
              <div className="section-header">
                <input
                  type="text"
                  className="search-input"
                  placeholder=" Tm kim s kin..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                />
                <button
                  className="btn btn-export-sm"
                  onClick={() => handleExportEvents("csv")}
                >
                   Xut CSV
                </button>
              </div>

              <div className="events-grid">
                {filteredEvents.map((event) => (
                  <div key={event._id} className="event-admin-card">
                    <div className="event-admin-header">
                      <h3>{event.title}</h3>
                      <span className={`status-badge status-${event.status}`}>
                        {event.status === "upcoming"
                          ? "Sp din ra"
                          : event.status === "completed"
                          ? " hon thnh"
                          : event.status}
                      </span>
                    </div>
                    <div className="event-admin-body">
                      <p> {event.location}</p>
                      <p>
                         {new Date(event.date).toLocaleDateString("vi-VN")}
                      </p>
                      <p>
                         {event.registered || event.participants?.length || 0}{" "}
                        / {event.maxParticipants} ngi
                      </p>
                      <p>
                         Ngi to:{" "}
                        {event.creator?.fullName || event.creator?.username}
                      </p>
                    </div>
                    <div className="event-admin-actions">
                      <button
                        className="btn btn-danger-sm"
                        onClick={() => handleDeleteEvent(event._id)}
                      >
                         Xa
                      </button>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          )}
        </div>
      </div>

      {/* User Edit Modal */}
      {showUserModal && selectedUser && (
        <div className="modal-overlay" onClick={() => setShowUserModal(false)}>
          <div className="modal-content" onClick={(e) => e.stopPropagation()}>
            <div className="modal-header">
              <h2>Chnh sa ngi dng</h2>
              <button
                className="modal-close-btn"
                onClick={() => setShowUserModal(false)}
              >
                
              </button>
            </div>
            <div className="modal-body">
              <div className="form-group">
                <label>Tn ng nhp:</label>
                <input type="text" value={selectedUser.username} disabled />
              </div>
              <div className="form-group">
                <label>Email:</label>
                <input type="text" value={selectedUser.email} disabled />
              </div>
              <div className="form-group">
                <label>Vai tr:</label>
                <select
                  value={selectedUser.role}
                  onChange={(e) =>
                    handleUpdateRole(selectedUser._id, e.target.value)
                  }
                >
                  <option value="volunteer">Tnh nguyn vin</option>
                  <option value="event_manager">Qun l s kin</option>
                  <option value="admin">Qun tr vin</option>
                </select>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Notification */}
      {notification && (
        <Notification
          type={notification.type}
          title={notification.title}
          message={notification.message}
          onClose={() => setNotification(null)}
        />
      )}

      <Footer />
    </div>
  );
};

export default AdminDashboard;
