// Events Service - Kt ni vi MongoDB thng qua backend API
import axios from "axios";

const API_URL = "http://localhost:5000/api/events";

// Ly token t localStorage
const getAuthHeader = () => {
  const token = localStorage.getItem("token");
  return token ? { Authorization: `Bearer ${token}` } : {};
};

// Listeners  notify khi data thay i
const listeners = new Set();

// Notify tt c listeners
const notifyListeners = () => {
  listeners.forEach((callback) => callback());
};

export const eventsService = {
  // Ly tt c s kin
  getAllEvents: async (filters = {}) => {
    try {
      const params = new URLSearchParams();
      if (filters.category && filters.category !== "all") {
        params.append("category", filters.category);
      }
      if (filters.status) {
        params.append("status", filters.status);
      }
      if (filters.search) {
        params.append("search", filters.search);
      }

      const response = await axios.get(`${API_URL}?${params.toString()}`, {
        headers: getAuthHeader(),
      });
      return response.data.data || [];
    } catch (error) {
      throw error;
    }
  },

  // Ly s kin theo ID
  getEventById: async (id) => {
    try {
      const response = await axios.get(`${API_URL}/${id}`, {
        headers: getAuthHeader(),
      });
      return response.data.data;
    } catch (error) {
      throw error;
    }
  },

  // To s kin mi
  createEvent: async (eventData) => {
    try {
      const response = await axios.post(`${API_URL}`, eventData, {
        headers: getAuthHeader(),
      });
      notifyListeners();
      return response.data.data;
    } catch (error) {
      throw error;
    }
  },

  // Cp nht s kin
  updateEvent: async (id, updatedData) => {
    try {
      const response = await axios.put(`${API_URL}/${id}`, updatedData, {
        headers: getAuthHeader(),
      });
      notifyListeners();
      return response.data.data;
    } catch (error) {
      throw error;
    }
  },

  // Xa s kin
  deleteEvent: async (id) => {
    try {
      await axios.delete(`${API_URL}/${id}`, {
        headers: getAuthHeader(),
      });
      notifyListeners();
      return true;
    } catch (error) {
      throw error;
    }
  },

  // ng k tham gia s kin
  registerForEvent: async (id) => {
    try {
      const response = await axios.post(
        `${API_URL}/${id}/register`,
        {},
        {
          headers: getAuthHeader(),
        }
      );
      notifyListeners();
      return response.data.data;
    } catch (error) {
      throw error;
    }
  },

  // Hy ng k s kin
  unregisterFromEvent: async (id) => {
    try {
      const response = await axios.delete(`${API_URL}/${id}/unregister`, {
        headers: getAuthHeader(),
      });
      notifyListeners();
      return response.data.data;
    } catch (error) {
      throw error;
    }
  },

  // Ly cc s kin  ng k
  getMyRegisteredEvents: async () => {
    try {
      const response = await axios.get(`${API_URL}/my/registered`, {
        headers: getAuthHeader(),
      });
      return response.data.data || [];
    } catch (error) {
      throw error;
    }
  },

  // Ly cc s kin do mnh to
  getMyCreatedEvents: async () => {
    try {
      const response = await axios.get(`${API_URL}/my/created`, {
        headers: getAuthHeader(),
      });
      return response.data.data || [];
    } catch (error) {
      throw error;
    }
  },

  // Ly danh sch ng k ch ph duyt
  getPendingRegistrations: async (eventId) => {
    try {
      const response = await axios.get(
        `${API_URL}/${eventId}/registrations/pending`,
        {
          headers: getAuthHeader(),
        }
      );
      return response.data;
    } catch (error) {
      throw error;
    }
  },

  // Ly tt c ng k ca event
  getEventRegistrations: async (eventId) => {
    try {
      const response = await axios.get(`${API_URL}/${eventId}/registrations`, {
        headers: getAuthHeader(),
      });
      return response.data;
    } catch (error) {
      throw error;
    }
  },

  // Ph duyt ng k
  approveRegistration: async (eventId, userId) => {
    try {
      const response = await axios.put(
        `${API_URL}/${eventId}/registrations/${userId}/review`,
        { status: "approved" },
        {
          headers: getAuthHeader(),
        }
      );
      notifyListeners();
      return response.data;
    } catch (error) {
      throw error;
    }
  },

  // T chi ng k
  rejectRegistration: async (eventId, userId) => {
    try {
      const response = await axios.put(
        `${API_URL}/${eventId}/registrations/${userId}/review`,
        { status: "rejected" },
        {
          headers: getAuthHeader(),
        }
      );
      notifyListeners();
      return response.data;
    } catch (error) {
      throw error;
    }
  },

  // Check-in tnh nguyn vin
  checkInParticipant: async (eventId, userId) => {
    try {
      const response = await axios.put(
        `${API_URL}/${eventId}/participants/${userId}/checkin`,
        {},
        {
          headers: getAuthHeader(),
        }
      );
      notifyListeners();
      return response.data;
    } catch (error) {
      throw error;
    }
  },

  // Hy check-in
  undoCheckIn: async (eventId, userId) => {
    try {
      const response = await axios.delete(
        `${API_URL}/${eventId}/participants/${userId}/checkin`,
        {
          headers: getAuthHeader(),
        }
      );
      notifyListeners();
      return response.data;
    } catch (error) {
      throw error;
    }
  },

  // nh du hon thnh
  markAsCompleted: async (eventId, userId) => {
    try {
      const response = await axios.put(
        `${API_URL}/${eventId}/participants/${userId}/complete`,
        {},
        {
          headers: getAuthHeader(),
        }
      );
      notifyListeners();
      return response.data;
    } catch (error) {
      throw error;
    }
  },

  // Hy hon thnh
  undoCompleted: async (eventId, userId) => {
    try {
      const response = await axios.delete(
        `${API_URL}/${eventId}/participants/${userId}/complete`,
        {
          headers: getAuthHeader(),
        }
      );
      notifyListeners();
      return response.data;
    } catch (error) {
      throw error;
    }
  },

  // Subscribe  nhn thng bo khi data thay i
  subscribe: (callback) => {
    listeners.add(callback);
    return () => listeners.delete(callback);
  },
};
